<template>
  <div id="app">
    <meting-js
      server="tencent" 
      type="playlist" 
      id="8574171521" 
      fixed="true" 
      autoplay="true" 
      loop="all" 
      order="list"
      preload="auto" 
      list-folded="true" 
      list-max-height="500px" 
      lrc-type="0"
      volume="1">
    </meting-js>
  </div>
  <!--
    server (平台) music platform: netease, tencent, kugou, xiami, baidu
    type (类型)  	song, playlist, album, search, artist
    id (编号)   
    fixed (固定底部模式)  enable fixed mode
    autoplay (自动播放)   audio autoplay
    loop (循环播放)   player loop play, values: ‘all’, ‘one’, ‘none’
    order (顺序)     player play order, values: ‘list’, ‘random’
    preload (加载)  values: ‘none’, ‘metadata’, ‘auto’
    list-folded (列表折叠)  indicate whether list should folded at first
    list-max-height (最大高度)  list max height
    lrc-type (歌词)   	indicate whether list should folded at first
    volume (声量) 0-1
  -->
</template>

<script>
  export default {
    name: 'App',
    components: {
      // HeadNav
    }
  }
  </script>




